<template>
  <vx-card>
    <div>
      <vs-button @click="popupActive2 = true" color="primary" type="filled"
        >Create</vs-button
      >
      <vs-popup
        classContent="popup-example"
        title="Create Country"
        :active.sync="popupActive2"
      >
        <div class="vx-row mb-6">
          <div class="vx-col sm:w-1/3 w-full">
            <span>Code</span>
          </div>
          <div class="vx-col sm:w-2/3 w-full">
            <vs-input
              class="w-full"
              icon-pack="feather"
              icon="icon-lock"
              icon-no-border
              v-model="code"
            />
          </div>
        </div>
        <div class="vx-row mb-6">
          <div class="vx-col sm:w-1/3 w-full">
            <span>Name</span>
          </div>
          <div class="vx-col sm:w-2/3 w-full">
            <vs-input
              class="w-full"
              icon-pack="feather"
              icon="icon-globe"
              icon-no-border
              v-model="name"
            />
          </div>
        </div>
        <div class="vx-row mb-6">
          <div class="vx-col sm:w-1/3 w-full">
            <span>Note</span>
          </div>
          <div class="vx-col sm:w-2/3 w-full">
            <vs-input
              class="w-full"
              icon-pack="feather"
              icon="icon-list"
              icon-no-border
              v-model="note"
            />
          </div>
        </div>

        <div class="vx-row">
          <div class="vx-col sm:w-2/3 w-full ml-auto">
            <vs-button class="mr-3 mb-2">Submit</vs-button>
            <vs-button
              color="warning"
              type="border"
              class="mb-2"
              @click="
                code = name = note = '';
                popupActive2 = false;
              "
              >Cancel</vs-button
            >
          </div>
        </div>
      </vs-popup>
    </div>

    <vs-table pagination :max-items="4" search :data="listCountry">
      <template slot="thead" >
        <vs-th class="text-center">INDEX</vs-th>
        <vs-th sort-key="name" class="text-center">NAME</vs-th>
        <vs-th sort-key="code" class="text-center">CODE</vs-th>
        <vs-th sort-key="note" class="text-center">NOTE</vs-th>
        <vs-th class="text-center">ACTION</vs-th>
      </template>

      <template slot-scope="{ data }">
        <vs-tr :key="indextr" v-for="(tr, indextr) in data">
           <vs-td >
            {{ indextr }}
          </vs-td>
          <vs-td :data="data[indextr].name" >
            {{ data[indextr].name }}
          </vs-td>

          <vs-td :data="data[indextr].code">
            {{ data[indextr].code }}
          </vs-td>

          <vs-td :data="data[indextr].note">
            {{ data[indextr].note }}
          </vs-td>

          <vs-td :data="data[indextr].createDate">
            {{ data[indextr].createDate | getDay }}
          </vs-td>
          <vs-td :data="data[indextr].id">
            <div class="flex">
              <vs-button
                type="border"
                size="small"
                icon-pack="feather"
                icon="icon-edit"
                color="success"
                class="mr-2"
              ></vs-button>
              <vs-button
                type="border"
                size="small"
                icon-pack="feather"
                icon="icon-trash"
                color="danger"
              ></vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>
  </vx-card>
</template>

